<template>
  <div>
    <div class="row mt-4"> 
            <div class="col-md-4 mb-4" v-for="item in products" :key="item.id">
                <div class="card " >
                     <div style="height: 150px; background-size: cover; background-position: center" :style="{backgroundImage:`url(${item.imageUrl})`}"></div>   
                     <div class="card-body">
                          <span class="badge badge-secondary float-right ml-2">{{item.category}}</span>
                          <h5 class="card-title">
                              <a href="#" class="text-dark">{{item.title}}</a>
                          </h5>
                          <p class="card-text">{{item.content}}</p>
                          <div class="d-flex justify-content-between align-items-baseline">
                               <div class="h5" v-if="!item.price">{{item.origin_price}}</div>                  
                               <del class="h6" v-if="item.price">{{item.origin_price}}</del>                   
                               <div class="h5" v-if="item.price">{{item.price}}</div>                          
                          </div>
                    </div>

                    <div class="card-footer d-flex">
                         <button type="button" class="btn btn-outline-secondary btn-sm" @click="getProduct(item.id)">
                               <i class="fas fa-spinner fa-spin" v-if="status.loadingItem==item.id"></i>
                               查看更多
                         </button>
                         <button type="button" class="btn btn-outline-danger btn-sm ml-auto" @click="addtoCart(item.id)">
                               <i class="fas fa-spinner fa-spin" v-if="status.loadingItem==item.id"></i>
                               加到購物車
                         </button>
                    </div>

                 </div>
             </div>
         </div>
          <div class="modal fade" id="productModal" >
              <div class="modal-dialog modal-lg" >
                   <div class="modal-content">

                        <div class="modal-header">
                              <h5 class="modal-title" >{{product.title}}</h5>
                              <button type="button" class="close" data-dismiss="modal" >
                                       <span >&times;</span>
                              </button>
                        </div>

                        <div class="modal-body">
                              <img :src="product.imageUrl" class="img-fluid" alt=""> 
                              <blockquote class="blockquote mt-3">                                                      
                                     <p class="mb-0">{{product.content}}</p>
                                     <footer class="blockquote-footer text-right">{{product.description}}</footer>      
                              </blockquote>
                              <div class="d-flex  justify-content-between  align-items-baseline">
                                    <div class="h4" v-if="!product.price">{{product.origin_price}}元</div>
                                    <div class="h6" v-if="product.price">原價{{product.origin_price}}元</div>
                                    <div class="h4" v-if="product.price">現在只要{{product.price}}元</div>
                              </div>
                              <select name="" class="form-control mt-3 py-0" v-model="product.num">                    
                                    <option :value="num" v-for="num in 10" :key="num">                                  
                                        選購{{num}}{{product.unit}}
                                    </option>
                             </select>
                        </div>

                       <div class="modal-footer">
                             <div class="text-muted  mr-3">
                                   小計<strong>{{product.num*product.price}}</strong>元
                             </div>
                             <button type="button" class="btn btn-primary"  @click="addtoCart(product.id,product.num)">
                                   <i class="fas fa-spinner fa-spin" v-if="status.loadingItem==product.id"></i>
                                   加到購物車
                             </button>
                       </div> 

                  </div> 
            </div> 
       </div>
        <div class="my-5 row justify-content-center">
              <table class="table">
                   <thead>
                          <th></th>
                          <th>品名</th>
                          <th>數量</th>
                          <th>單價</th>
                   </thead>
                   <tbody>  
                          <tr v-for="item in cart.carts" :key="item.id" v-if="cart.carts">
                               <td class="align-middle">
                                     <button type="button" class="btn btn-outline-danger btn-sm" @click=" removeCartItem(item.id)">
                                             <i class="far fa-trash-alt"></i>
                                     </button>
                               </td>
                               <td class="align-middle">
                                   {{item.product.title}}
                                    <div class="text-success" v-if="item.coupon">
                                         已套用優惠劵
                                    </div>
                              </td>
                              <td class="align-middle">{{item.qty}}/{{item.product.unit}}</td>
                              <td class="align-middle text-right">{{item.final_total}}</td>
                         </tr>
                   </tbody>
                   <tfoot>
                         <tr>
                              <td colspan="3" class="text-right">總計</td>
                              <td class="text-right">{{cart.total}}</td>
                         </tr>
                         <tr v-if="cart.final_total!==cart.total">
                             <td colspan="3" class="text-right text-success">折扣價</td>
                             <td class="text-right text-success">{{cart.final_total}}</td>
                         </tr>
                  </tfoot>
             </table>
             <div class="input-group mb-3 input-group-sm">
                   <input type="text" class="form-control" v-model="coupon_code" placeholder="請輸入優惠碼">
                   <div class="input-group-append">
                         <button class="btn btn-outline-secondary" type="button" @click="addCouponCode">
                            套用優惠碼
                         </button>
                  </div>
            </div>

      </div> 
      <div class="my-5 row justify-content-center">
              <form class="col-md-6 " @submit.prevent="createOrder">
                      <div class="form-group">
                            <label for="useremail">Email</label>
                            <input type="email" class="form-control" name="email" id="useremail" v-model="form.user.email" placeholder="請輸入 Email" required>
                            <span class="text-danger"></span>
                      </div>
  
                      <div class="form-group">
                            <label for="username">收件人姓名</label>
                            <input type="text" class="form-control" name="name" id="username"  v-model="form.user.name" placeholder="輸入姓名">
                            <span class="text-danger"></span>
                      </div>
  
                      <div class="form-group">
                            <label for="usertel">收件人電話</label>
                            <input type="tel" class="form-control" id="usertel" v-model="form.user.tel" placeholder="請輸入電話">
                      </div>
  
                     <div class="form-group">
                            <label for="useraddress">收件人地址</label>
                            <input type="text" class="form-control" name="address" id="useraddress" v-model="form.user.address"  placeholder="請輸入地址">
                            <span class="text-danger">地址欄位不得留空</span>
                     </div>
  
                     <div class="form-group">
                            <label for="comment">留言</label>
                            <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
                     </div>
                     <div class="text-right">
                            <button class="btn btn-danger">送出訂單</button>
                     </div>
             </form>
      </div>
        
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  name: 'HelloWorld',
  data () {
           return{
                    products:[],
                      product:{},
                      cart:{},
                      coupon_code:'',
                    form:{  user:{
                              name: "",
                              email: "",
                              tel: "",
                              address: ""
                      },
                        message: ""
                    },
                    status:{
                           loadingItem:''
                    }
           }
  },
  methods:{
          getProducts(){
                            const url=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/products`;
                            const vm=this;
                           this.$http.get(url).then((response)=>{
                              vm.products=response.data.products;
                   
                           });
          },
          getProduct(id){
                            const url=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/product/${id}`;
                            const vm=this;
                             vm.status.loadingItem=id;
                           this.$http.get(url).then((response)=>{
                                 vm.status.loadingItem='';
                              vm.product=response.data.product;
                          $("#productModal").modal("show")
                           });
          },
          addtoCart(id,qty=1){
                               const url=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/cart`;
                            const vm=this;
                            vm.status.loadingItem=id;
                            const form={
                                  product_id:id,
                                  qty
                            }
                           this.$http.post(url,{data:form}).then((response)=>{
                               console.log(response.data);
                               vm.status.loadingItem='';
                               $("#productModal").modal("hide");
                               this.getCart();
                           });
          }, getCart(){
                        const url=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/cart`;
                            const vm=this;
                           
                           this.$http.get(url).then((response)=>{
                               vm.cart=response.data.data;
                           });
          },removeCartItem(id){
                               const url=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/cart/${id}`;
                            const vm=this;
                           
                           this.$http.delete(url).then((response)=>{
                              console.log(response.data)
                           });
          },Newcoupon(){
                               const url=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/admin/coupon`;
                            const vm=this;
                           const form={
                                       percent: 80,
                                        code: "Code"    
                           }
                           this.$http.post(url,{data:form}).then((response)=>{
                              console.log(response.data)
                           });                    

                    },
    addCouponCode(){
                         const url=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/coupon`;
                            const vm=this;
                           const form={
                                       
                                        code: "Code"    
                           }
                           this.$http.post(url,{data:form}).then((response)=>{
                              console.log(response.data)
                           });        
                    },
                    createOrder(){
                                   const url=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/order`;
                            const vm=this;
                        
                           this.$http.post(url,{data:vm.form}).then((response)=>{
                              console.log(response.data)
                                     this.$router.push(`/CustomerCheckout/${response.data.orderId}`);
                                
                           });     
                    }


  },
  created(){
          this.getProducts();
          this.getCart();
          this.Newcoupon();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



